<template>
  <div id="app">
    <b-navbar toggleable="lg" class="app-navbar" type="dark" variant="danger">
    <b-navbar-brand  to='/' class="">
      <img src="./assets/image/logo(1).png" style="width: 250px;">
    </b-navbar-brand>
    <b-navbar-toggle target="nav-collapse"></b-navbar-toggle>
  <b-collapse id="nav-collapse" is-nav>
    <b-navbar-nav class="w-100">
      <div class=" d-flex flex-column flex-lg-row justify-content-lg-start">
        <b-nav-item class="mx-3" to='/news'><b-nav-text><a>本店訊息</a></b-nav-text></b-nav-item>
        <b-nav-item class="mx-3" to='/introduction'><b-nav-text><a>鍋物介紹</a></b-nav-text></b-nav-item>
        <b-nav-item class="mx-3" to='/cart'><b-nav-text><a>我要點餐</a></b-nav-text></b-nav-item>
        <b-nav-item class="mx-3" to='/wait'><b-nav-text><a>我要候位</a></b-nav-text></b-nav-item>
        <b-nav-item class="mx-3" to='/comment'><b-nav-text><a>留言板</a></b-nav-text></b-nav-item>
      </div>
      <div class="ml-md-auto ml-3 d-flex flex-column flex-lg-row me lg-container">
        <b-nav-item class="mx-lg-auto" v-if="!user.isLogin" @click="signForLine"><b-nav-text><a>註冊</a></b-nav-text></b-nav-item>
        <b-nav-item class="ms-lg-auto" v-if="user.isLogin && !user.isAdmin">
        <b-nav-item-dropdown text="管理" right>
          <b-dropdown-item class="ms-lg-auto" to='/ordercart'>購物車<b-badge class="mx-2" variant="danger">{{ user.cart }}</b-badge></b-dropdown-item>
          <b-dropdown-item class="ms-lg-auto" to='/orders'>訂單明細</b-dropdown-item>
        </b-nav-item-dropdown>
      </b-nav-item>
      <b-nav-item class="ms-lg-auto" v-if="user.isLogin && user.isAdmin">
        <b-nav-item-dropdown text="管理" right>
          <b-dropdown-item to='/admin/waits'>候位管理</b-dropdown-item>
          <b-dropdown-item to='/admin/orders'>訂單管理</b-dropdown-item>
          <b-dropdown-item to='/admin/products'>商品管理</b-dropdown-item>
          <b-dropdown-item to='/admin/news'>公告管理</b-dropdown-item>
        </b-nav-item-dropdown>
      </b-nav-item>
      <b-nav-item class="ms-lg-auto d-none d-lg-block"><b-nav-text><a>|</a></b-nav-text></b-nav-item>
      <b-nav-item class="ms-lg-auto" v-if="!user.isLogin" v-b-modal.modal-1 ><b-nav-text><a>登入</a></b-nav-text></b-nav-item>
      <b-nav-item class="ms-lg-auto" v-if="user.isLogin" @click="logout"><b-nav-text><a>登出</a></b-nav-text></b-nav-item>
      </div>
    </b-navbar-nav>
    </b-collapse>
  </b-navbar>

<b-modal ref="my-modal" id="modal-1" centered hide-footer @hidden="hideModal">
    <b-tabs align="around">
    <b-tab title="使用者登入" active>
    <div class="text-center my-5">
      <font-awesome-icon @click="signForLine" :icon="['fab', 'line']" size="6x" style="color:green"/>
    </div>
    </b-tab>
    <b-tab title="管理者登入">
      <b-form @submit.prevent="login">
        <b-form-group label='帳號' label-for='input-account' description='帳號長度為 5 到 20 個字' :state='state.account' invalid-feedback="帳號格式不正確">
        <b-form-input id="input-account" v-model="form.account" required placeholder='請輸入帳號' type="text" :state='state.account'></b-form-input>
        </b-form-group>
        <b-form-group label='密碼' label-for='input-account' description='帳號長度為 4 到 20 個字' :state='state.account' invalid-feedback="帳號格式不正確">
        <b-form-input id="input-account" v-model="form.password" required placeholder='請輸入密碼' type="password" :state='state.password'></b-form-input>
        </b-form-group>
        <div class="text-center">
          <b-btn class="mx-1" variant='success' type='sumbit'>登入</b-btn>
        </div>
      </b-form>
    </b-tab>
  </b-tabs>
  </b-modal>
  <router-view :key='$route.fullPath'></router-view>
  </div>
</template>

<style>
@import url('https://fonts.googleapis.com/css2?family=Noto+Sans+TC:wght@100;300;400;500;700;900&display=swap');
</style>

<script>
export default {
  data () {
    return {
      form: {
        account: '',
        password: ''
      }
    }
  },
  computed: {
    state () {
      return {
        account: this.form.account.length === 0 ? null : this.form.account.length >= 4 && this.form.account.length <= 20,
        password: this.form.password.length === 0 ? null : this.form.password.length >= 4 && this.form.password.length <= 20
      }
    }
  },
  methods: {
    hideModal () {
      this.form.account = ''
      this.form.password = ''
    },
    random (R) {
      return Math.floor(Math.random() * R)
    },
    async signForLine () {
      let link = 'https://access.line.me/oauth2/v2.1/authorize?'
      link += 'response_type=code' // 使用者登入後，請LINE回傳「code」（授權碼）
      link += '&client_id=' + process.env.VUE_APP_CHANNEL_ID
      link += '&redirect_uri=' + process.env.VUE_APP_CALLBACK_URL // /users/signInLine
      link += '&state=' + this.randomState
      link += '&bot_prompt=normal' // 預設要加官方帳號好友
      link += '&scope=openid%20profile' // 預設申請使用者資料及 token
      window.location.href = link
    },
    login () {
      this.$refs['my-modal'].hide()
      this.$store.dispatch('user/login', this.form)
    },
    logout () {
      this.$store.dispatch('user/logout')
    }
  },
  async created () {
    this.$store.dispatch('user/getInfo')
    this.$store.dispatch('user/signInLine')
  }
}
</script>
